.form {
  display: flex;
  flex-direction: column;
  gap: 5rem;
  padding: 1.6rem;

  width: 50%;
}

.form_header,
.button {
  width: 100%;
  display: block;
  font-family: "BPG Arial Caps", sans-serif;
  font-size: 2rem;
  letter-spacing: 0.2rem;
  color: #857a72;
  line-height: 2;
  font-weight: 400;
}
.label {
  width: 100%;
  display: block;
  font-family: "BPG Arial Caps", sans-serif;
  font-size: 1.6rem;
  letter-spacing: 0.2rem;
  color: #857a72aa;
  line-height: 2;
  font-weight: 400;
  display: flex;
}

.button,
.input {
  display: flex;
  width: 100%;
  border-radius: 8px;

  color: #000000;
  font-size: 16px;
  padding: 1.5rem 3rem;
  text-decoration: none;
  transition: all 0.4s;

  font-family: "BPG Arial Caps", sans-serif;
}
.input {
  border: solid #fff 1px;
}

.button {
  border: solid #65783a 1px;
  justify-content: center;
  letter-spacing: 0.5rem;
}

.input:hover,
.button:hover {
  background: #fff;
}

.input:focus {
  border: solid #65783a 1px;
}

@media screen and (max-width: 1200px) {
  .form {
    width: 100%;
    justify-content: center;
  }
  .button,
  .label,
  .input {
    justify-content: center;
  }
}
